import Cell from 'vux/src/components/cell/index.vue'

  export default{
    components: {
      XInput,
      XButton,
      Group,
      Cell
    },
    props: ["mask"],
    data(){
      return {
        isCheck: true,   //是否选中checkBox
        isShowTemp: false, //是否显示页面
        mobileNumber: '', //输入的电话号码
        codeNum: '',      //输入的验证码
        messageId : "",   //发送短信后的信息id
        // getCodeBtn: 'display:none',
        isGotMessageId: false,  //是否已获取过验证码

        // 验证码
        waiting: false,
        coolWaiting: "coolWaiting",
        second: 60,

        //点击输入框
        focusInput: 0
      }
    },
    mounted(){
      this.showLoading();
      debugger ;
      var _self = this;//保存指向
      var user = M.F.D.get("user");
      if(user.isBindMobile){
        //M.checkPath();
        console.log(11);
      }
      _self.$emit("$showToolBar",false);

        var _self = this;
        //this.H.toAjax(true).then(function(data){
          //_self.A.post("/v1/auth/get",data).then(function(result) {
            //if(result.data.state.code == 0){
                //if(!result.data.data.isBindMobile){
                  // 跳转到
//              if(result.data.data.isBindMobile){

                   _self.isShowTemp = true ;

                  _self.hideLoading();

                //}else{
                 //  _self.hideLoading();
                 // M.F.D.set("isBindMobile",true);
                 // _self.$router.push("/admin/index");
                //}
           // }else{
            //   _self.hideLoading();
            //  _self.$emit("modalAlert",result.data.state.msg);
           // }

         // })
        //})

    },
    methods: {
      goAgreement(){
        this.$router.push("/admin/agreement");
      },
      changeBorder(val){
        this.focusInput = Number(val);
      },
      doCheck(){
        this.isCheck = !this.isCheck;
        alert(this.isCheck)
      },
      doSign(event){

        //阻止没填写任何信息就提交
        if(this.mobileNumber.trim().length == 0 || this.codeNum.trim().length == 0){
          return ;
        }

       //验证手机号
       var mobileNumber = this.mobileNumber;
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(mobileNumber))) {
          this.$emit("modalAlert","请输入正确的手机号！")
          return ;
        }

        //是否获取过验证码
        if(!this.isGotMessageId){
          this.$emit("modalAlert","请先获取验证码")
          return ;
        }

//        //检查验证码是否过期
//        if(this.messageId == "" || this.messageId.length == 0) {
//          this.$emit("modalAlert","验证码已过期,请重新获取");
//          return ;
//        }
//        //检查是否已选中用户协议
//        if(!this.isCheck) {
//          this.$emit("modalAlert","请先阅读并同意知识圈用户协议");
//          return ;
//        }

        //提交验证码绑定手机
        var codeNum = this.codeNum;
        if (mobileNumber.length > 0 && codeNum.length > 0 && this.messageId ) {
          //验证账号

          var _self = this ;
          var obj = {mobileNumber :this.mobileNumber ,code :this.codeNum ,messageId: this.messageId };

          this.H.toAjax(true).then(function (data) { //data 公用参数
            var submitData = data;

            submitData.data = obj;
            //请求该打卡详细信息
            _self.A.post("/v1/user/bindMobileWithCode", submitData).then(function (result) {
              console.warn(result);
              if (result.data.state.code == 0) {
                var path = M.F.D.get("pathname");

                _self.$router.push("/admin/index");
              }else{
                 _self.$emit("modalAlert",result.data.state.msg);
                 //_self.messageId = "" ;
                 _self.code = "";
              }
            });
          });



        }
      },
      //获取验证码
      getVerticalCode(){
        //ajax获取验证码
        console.log("===========smsCode============");
        var mobileNumber = this.mobileNumber;

        //是否填写手机号
        if(this.mobileNumber.trim().length == 0) {
          this.$emit("modalAlert","请输入手机号！")
          return ;
        }

        //手机号是否正确
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(mobileNumber))) {
          this.$emit("modalAlert","请输入正确的手机号！")
          return ;
        }

        var _self = this ;
        var obj = {mobileNumber :this.mobileNumber };

         this.H.toAjax(true).then(function (data) { //data 公用参数
            var submitData = data;
            submitData.data = obj;
            //请求该打卡详细信息
            _self.A.post("/v1/user/smsCode", submitData).then(function (result) {
              console.warn(result);
              if (result.data.data && result.data.state.code == 0) {
                _self.isGotMessageId = true;
                _self.messageId = result.data.data.messageId;
                  window.countTime = setTimeout((function () {
                  _self.waiting = true;
                  _self.$runSecond(_self.second);
                }).bind(_self), 1000);
              }else{
                 _self.$emit("modalAlert", result.data.state.msg);
                 //  clear
                _self.messageId = "";
              }
            });
          });
      },
      verticalCode: function () {
      },
      //递归 setTimeout 计时
      $runSecond: function (second) {
        if (second == 0) {
          //this.$getSecurityCodeUrl();
          this.waiting = false;
          this.messageId = "";//过期
          this.second = 60;
          clearInterval(window.countTime);
        } else {
          this.second = second;
          this.clearSecondTimer = setTimeout((function () {
            this.$runSecond(--second);
          }).bind(this), 1000);
        }
      }
    }
  }



